import { Sensor } from './../interfaces/SensorData';
import { HttpClient, HttpErrorResponse, HttpEvent, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError, retry } from 'rxjs/operators';

const post_url = 'http://52.16.82.127:4000/new_sensor';

@Injectable({
  providedIn: 'root'
})
export class SensorsService {
 httpOptions = {
    headers: new HttpHeaders({
      'Content-Type':  'application/json',
    })
  };

  constructor(private http: HttpClient) { }

  addSensor (new_sensor: Sensor): Observable<Sensor> {
    console.log(JSON.stringify(new_sensor));
    return this.http.post<Sensor>(post_url, JSON.stringify(new_sensor), this.httpOptions).pipe();
  }
}