export class PersonalData {
  Name: string;
  Age: number;
  Gender: string;
  HighRiskGroup: boolean;
}

export class HospitalizationInfo {
  ReceptionTime;
  ReleaseTime;
  HospitalizationStatus;
  Comments;
}

export class PersonSettings {
  // settings info
  minHR: number;
  maxHR: number;
  minBR: number;
  maxBR: number;
  minSpO2: number;
  maxSpO2: number;
  //sensors;
}

export class AlertData {
  name: string;
  time: Date;
}

export class PersonHistory {
  alerts: AlertData[];
  scoring: number[];
}

export class DBPatient{
  patient_Id: string;
  time_tag: string;
  first_name: string;
  last4Digit: string;
  institute_name: string;
  department_name: string;
  room_identifier: string;
  bed_identifier: string;
  bed_location: string;
  date_of_birth: string;
  risk_group: string;
  weight: string;
  gender: string;
  normal_range_list;
  sensors_list;
}

export class MeasureResultsUI{
  heartRate;
  bloodPresure;
  spO2;
  breathingRate;
  extraO2;
  fever;
  breathingInfo;
}

export class PersonHealthData {
  patient_Id;
  time_tag;
  first_name: string;
  last4Digit: string;
  institute_name: string;
  department_name: string;
  room_identifier: string;
  bed_identifier: string;
  bed_location: string;
  date_of_birth: string;
  // risk_group: string;
  weight: number;
  gender: string;
  personal: PersonalData;

  hospitalization: HospitalizationInfo;

  measureResults: MeasureResultsUI;

  alerts;
  progress;

  score;
  history: PersonHistory;
  settings: PersonSettings;
  sensors_list;
}

