import { DBPatient } from './../interfaces/PersonData';
import { HttpClient, HttpErrorResponse, HttpEvent, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError, retry } from 'rxjs/operators';

const post_url = 'http://52.16.82.127:4000/new_patient';

@Injectable({
  providedIn: 'root'
})
export class PatientsService {
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type':  'application/json',
    })
  };

  constructor(private http: HttpClient) { }

  addPatient(new_patient: DBPatient): Observable<DBPatient> {
    return this.http.post<DBPatient>(post_url, JSON.stringify(new_patient), this.httpOptions).pipe();
  }

}
